body {
  margin: 0px;
  text-align: center;
  padding: 0;
  font-family: -apple-system, SF-Pro-Display, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@font-face {
  font-family: 'Santana Black';
  src: local('Santana-Black'), url(/fonts/Santana-Black.ttf) format('truetype');
}

@font-face {
  font-family: 'San Francisco';
  src: local('San Francisco'), url(/fonts/SF-Pro-Display.ttf) format('truetype');
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  /* border: red solid 1px; */
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.mapboxgl-canvas {
  border: 5px solid black;
  text-align: justify;
}

.mapboxgl-map {
  /* border: 5px solid red; */
  text-align: justify;
}

button {
  border: 2px solid black;
}

input {
  border: 2px solid black;
}

.Title {
  font-family: 'Orbitron', sans-serif;
}

.button-box {
  margin-top: 10px; 
}

.button-box button {
  padding: 4px;
  margin: 8px;
}
.Title {
  font-family: 'Santana-Black';
}

.img-fluid {
  margin-right: 15px;
}

.frappy {
  border: 2px solid black;
  margin-top: 16px;
  margin-bottom: 16px;
}

.card-img {
  display: none;
}

.countdown {
  margin-top: 8px;
  font-size: 22px;
}
